:host {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.flex-1 {
  flex: 1 1 auto;
  min-height: 0; /* Important for Firefox */
}

::ng-deep {
  .p-component-outlet {
    height: 100%;
  }

  .p-component-outlet > * {
    height: 100%;
    display: block;
  }

  .state-popover {
    background: var(--input-background-color);
    animation: fadeIn 0.2s ease-in-out;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hover:hover {
  opacity: 1 !important;
  color: var(--color-primary-color);
}
